//状态管理仓库

import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        topics: [ //tab切换
            { name: 'all', desc: '全部' }, //0
            { name: 'good', desc: '精华' }, //1
            { name: 'share', desc: '分享' },
            { name: 'ask', desc: '问答' },
            { name: 'job', desc: '招聘' },
            { name: 'dev', desc: '测试' }
        ],
        isShowMoreMenus: false, //actionsheet默认显示与否
        // moreMenus: , //需求：如果是未登录状态，显示‘请登录’；如果已经登陆，显示‘个人中心、消息通知、退出’
        isLogined: false, //登陆状态，默认未登录
        unLoginedMenus: {
            login: '请先登录'
        },
        loginedMenus: {
            center: '个人中心',
            create: '发表新话题',
            msg: '消息中心',
            logout: '退出'
        },
        loginname: '', //登录用户名
        accesstoken: '', //登录账号
        authorId: '', //登录id
    },
    getters: {
        moreMenus(state) { //参数state表示当前store对象的state数据
            if (state.isLogined) {
                return state.loginedMenus
            }
            return state.unLoginedMenus
        }
    },
    mutations: {
        //修改actionsheet的显示与否
        mutationIsShowMoreMenus(state, payload) {
            state.isShowMoreMenus = payload;
        },
        mutationIsLogined(state) {
            state.isLogined = true;
        },
        //修改登录名
        mutationLoginname(state, payload) {
            state.loginname = payload;
        },
        //修改accesstoken
        mutationAccesstoken(state, payload) {
            state.accesstoken = payload;
        },
        //修改登录账号Id
        mutationAuthorId(state, payload) {
            state.authorId = payload;
        },
        //登出操作
        mutationLogout(state) {
            state.isLogined = false;
            state.loginname = '';
            state.accesstoken = '';
            state.authorId = '';
        }
    },
    actions: {}
})

export default store;


